import { render, component, map, onKeyDown, AUTOFOCUS } from "ivi";
import { createBox } from "ivi-state";
import { div } from "ivi-html";
import { Game, LEFT, RIGHT, UP, DOWN } from "./state";
function cellClasses(flags) {
    if (flags & 1 /* Body */) {
        if (flags & 2 /* Head */) {
            return "Cell body head";
        }
        return "Cell body";
    }
    else if (flags & 4 /* Food */) {
        return "Cell food";
    }
    return "Cell";
}
const CELL_SIZE = 30;
const GameView = component(() => {
    let game;
    const keyDown = onKeyDown((ev) => {
        switch (ev.native.keyCode) {
            case 37 /* ArrowLeft */:
                game.setNewDirection(LEFT);
                return 1 /* PreventDefault */;
            case 38 /* ArrowUp */:
                game.setNewDirection(UP);
                return 1 /* PreventDefault */;
            case 39 /* ArrowRight */:
                game.setNewDirection(RIGHT);
                return 1 /* PreventDefault */;
            case 40 /* ArrowDown */:
                game.setNewDirection(DOWN);
                return 1 /* PreventDefault */;
        }
        return;
    });
    return (props) => (game = props.value,
        div(game.gameOver ? "SnakeGame gameOver" : "SnakeGame").c(div("Grid", {
            "tabIndex": 0,
            "autofocus": AUTOFOCUS(true),
        }, {
            "width": `${CELL_SIZE * game.grid.cols}px`,
            "height": `${CELL_SIZE * game.grid.rows}px`,
        }).e(keyDown).c(map(game.grid.cells, (c, i) => div(cellClasses(c)).k(i)))));
});
const CONTAINER = document.getElementById("app");
const GAME = new Game();
function tick() {
    GAME.updateState();
    render(GameView(createBox(GAME)), CONTAINER);
    setTimeout(tick, 100);
}
tick();
